import styled, { css } from 'react-emotion';
import propStyles from 'prop-styles';
import { family, palette } from 'css/variables';
import { rem } from 'polished';

export const Label = styled('label')`
    display: block;
    font-size: ${rem('12px', '16px')};
    text-transform: uppercase;
    margin-bottom: 6px;
`

export const FormGroup = styled('div')`
    display: table;
    width: 100%;
    margin: 0 0;
    padding: 0 0;
    border-top: 1px solid ${palette.black};
    border-bottom: 1px solid ${palette.black};
`;

export const InputGroup = styled('div')`
    display: table-cell;
    vertical-align: bottom;
    padding: 0 9px;
    &:first-of-type {
        padding-left: 0;
        width: 20%;
    }

    &:nth-child(2) {
        width: 60%;
    }

    &:last-of-type {
        padding-right: 0;
        width: 24%;
    }
`;

export const SelectWrapper = styled('div')`
    display: table-cell;
    vertical-align: bottom;
    padding-right: 9px;
    position: relative;
    z-index: 2;


    select {
        width: 100%;
    }

    &:before {
        content: '';
        top: 60%;
        right: 18px;
        position: absolute;
        width: 0px;
        height: 0px;
        background-color: ${palette.gray};
        border-top: 12px solid ${palette.black};
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        z-index: 0;
    }
`;


export const btnColor = {
    green: () => css`
        background-color: ${palette.green};
        color: ${palette.black};
    `,

    greenNeon: () => css`
        background-color: ${palette.greenNeon};
        color: ${palette.black};
    `,

    greenDarker: () => css`
        background-color: ${palette.greenDarker};
        color: ${palette.black};
    `,

    grayDarkest: () => css`
        background-color: ${palette.grayDarkest};
        color: ${palette.white};
    `,
};

export const btnOutline = {
    green: () => css`
        border-color: ${palette.green};
        color: ${palette.green};
    `,

    greenNeon: () => css`
        border-color: ${palette.greenNeon};
        color: ${palette.greenNeon};
    `,

    greenDarker: () => css`
        border-color: ${palette.greenDarker};
        color: ${palette.greenDarker};
    `,

    grayDarker: () => css`
        border-color: ${palette.grayDarkest};
        color: ${palette.grayDarkest};
    `,

};


export const Button = styled('button')`
    padding: 6px 8px;
    font-size: 14px;
    min-height: 40px;
    display: inline-block;
    vertical-align: middle;
    border-color: transparent;
    box-shadow: none;
    color: currentColor;
    background-color: transparent;
    ${family.raleway};
    font-weight: 700;
    border-radius: 2px;
    cursor: pointer;
    transition: background-color, border-color 0.2s ease-in-out;
    margin-right: 9px;
    -webkit-appearance: none;

    &:hover,
    &:active {
        opacity: 1;
    }

    &:focus {
        opacity: initial;
    }

    &:active {
        transform: translateY(1px);
    }

    &:disabled {
        opacity: 0.5;
        cursor: default;

        &:active {
            transform: translateY(0);
        }
    }

    ${propStyles({
        green: [btnColor.green],
        greenNeon:[btnColor.greenNeon],
        greenDarker:[btnColor.greenDarker],
        greenOutline: [btnOutline.green],
        greenNeon: [btnOutline.greenNeon],

    })};
`;



