import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Container } from 'css/basicElements';
import Logo from 'components/Logo'
import { palette } from 'css/variables';
import { lookupCard, lookupDDO, lookupMap,fetchRegistry } from 'connectors/actions/index';
import { change } from 'redux-form';

class Page extends Component {
  componentDidMount () {
      const { location,processQueryStringInit } = this.props;
      if(location)
        processQueryStringInit(location);
  }


  render() {
    const { right } = this.props;
    return (
      <Container>
        <div style={{float:"right"}}>
          {right}
        </div>
          <div className="logo-wrapper">
            <Logo
                fill={palette.green}
                width={320}
                style={{zIndex:-1}}
              />
            <div style={{color:"white"}}>
            Helsinki 2018
            </div>
          </div>
        {this.props.children}
      </Container>
    )
}
}

export default connect(
    state =>({
      ...state
    }),
    dispatch => ({
      processQueryStringInit : (location) => {
          const searchParams = new URLSearchParams(location.search);
          if (searchParams.get('sn')) {
              dispatch(change('lookupForm', 'sn', searchParams.get('sn')));
              if (searchParams.get('cmd') === 'hello') {
                dispatch(change('lookupForm', 'lookup', 'card'));
                dispatch(change('lookupForm', 'method', 'h18'));
                dispatch(lookupCard(dispatch,searchParams.get('sn')));
              }
              else {
                const lookup = searchParams.get('lookup');
                if (lookup) {
                  console.log("LOOKUP");
                  dispatch(change('lookupForm', 'lookup', lookup));
                  if(lookup === 'card' )
                    dispatch(lookupCard(dispatch,searchParams.get('sn')));
                  else if(lookup === 'map' )
                      dispatch(lookupMap(dispatch,searchParams.get('sn')));
                  else
                    dispatch(lookupDDO(dispatch,'h18',searchParams.get('sn')));

                }
                else {
                  dispatch(change('lookupForm', 'lookup', 'ddo'));
                  dispatch(lookupDDO(dispatch,'h18',searchParams.get('sn')));
                }
                dispatch(change('lookupForm', 'method', 'h18'));
              }
          }
          if (searchParams.get('cmd') === 'hello') {
              dispatch({
                  type: 'INDICATE_CARD_LOAD',
                  })
            }
            dispatch(fetchRegistry(dispatch))
      }

    })
)(Page);



