import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Container, OutputContainer} from 'css/basicElements';
import { palette } from 'css/variables';
import Page from 'containers/Page';
import { Form, Field, reduxForm } from 'redux-form';
import { FormGroup, InputGroup, SelectWrapper, Button, Label } from 'css/formElements';
import CardLoad from "containers/lookup/CardLoad";
import OutputBox from "containers/lookup/output/OutputBox";
import { resetOutputBox, submitValues,recordLastValue,lookupDDO,lookupCard,lookupMap } from 'connectors/actions';
import { change } from 'redux-form';
import { Link } from 'react-static';


import {alphaNumeric, maxLengthLimit, minLengthLimit, renderField } from './validation';
const resolvers = ['h18', 'sov', 'btcr','dom','erc275','ipid','stack','uport','v1'];

class LookUpForm extends Component {
    render () {
        const { cache, lookupChange, didChange, methodChange, pristine, handleSubmit, onSubmit, submitting, formValues } = this.props;

        let activeMethod;
        if (formValues.lookupForm)
            if (formValues.lookupForm.values)
                activeMethod = formValues.lookupForm.values.method;
        let activeSN;
        if (formValues.lookupForm)
            if (formValues.lookupForm.values)
                activeSN = formValues.lookupForm.values.sn;
        let lookupTarget;
        if (formValues.lookupForm)
            if (formValues.lookupForm.values)
                lookupTarget = formValues.lookupForm.values.lookup;
        return (
            <Form onSubmit={handleSubmit}>
                <FormGroup>
                  <SelectWrapper>
                  <Label htmlFor="method">Lookup</Label>
                      <Field
                          name="lookup"
                          component="select"
                          onChange={lookupChange(formValues,cache)}
                      >
                        <option key="1" value="ddo">DID Document</option>
                        {/*<option key="2" value="map">Use Case</option>*/}
                        <option key="3" value="card">H18 Card</option>
                      </Field>
                  </SelectWrapper>
                  <SelectWrapper>
                  <Label htmlFor="method">DID method</Label>
                      <Field
                          name="method"
                          component="select"
                          onChange={methodChange(formValues,cache)}
                          disabled={ lookupTarget !== 'ddo' }
                      >
                          {resolvers.map((resolver, i) => (
                              <option key={i}>
                                  {resolver}
                              </option>
                          ))}
                      </Field>
                  </SelectWrapper>
                    <InputGroup>
                        <Field
                            name="sn"
                            type="text"
                            component={renderField}
                            validate={[alphaNumeric, maxLengthLimit, minLengthLimit]}
                            maxLength={maxLengthLimit}
                            label="Identifier"
                            onChange={didChange(formValues,cache)}
                        />
                    </InputGroup>
                    <InputGroup>
                        <Button
                            greenOutline
                            disabled={pristine || submitting || !activeSN || activeSN === ''}
                            >
                            Go
                        </Button>
                    </InputGroup>
                </FormGroup>
            </Form>
        )
    }
};

LookUpForm = reduxForm({
    form: 'lookupForm',
})(LookUpForm);

LookUpForm = connect(
    state =>({
        formValues: state.form,
        initialValues: {
        },
        cache:state.cache
    }),
    dispatch => ({
      methodChange: (values,cache) => {
        //console.log("SHOULD CHANGE METHOD?:",values,cache);
        if(!values.lookupForm)
          return;
        if(!values.lookupForm.values)
          return;
        const V=values.lookupForm.values;
        dispatch(recordLastValue('method',V.method));
        if(!cache.method || !cache.lookup || !cache.sn)
            return;

        //console.log("METHOD CHANGE",cache);
        if(cache.method != V.method) {
          dispatch(resetOutputBox())
          if(V.sn && V.sn !== '' && V.method) {
            dispatch(lookupDDO(dispatch,V.method,V.sn));
          }
        }
      },
      lookupChange: (values,cache) => {
        //console.log("SHOULD CHANGE LOOKUP?:",values,cache);
        if(!values.lookupForm)
          return;
        if(!values.lookupForm.values)
          return;
        const V=values.lookupForm.values;
        dispatch(recordLastValue('lookup',V.lookup));
        if(!cache.method || !cache.lookup || !cache.sn)
            return;


        if(cache.lookup != V.lookup)
          dispatch(resetOutputBox());
        let method = V.method;
        if(V.lookup !== 'ddo') {
          if(V.method !== 'h18') {
            if(cache.lookup != V.lookup) {
              dispatch(change('lookupForm', 'method', 'h18'));
              method = 'h18';
            }
          }
        }
        if(cache.lookup !== V.lookup) {
          if(V.sn && V.sn !== '') {
            if(V.lookup === 'map') {
              dispatch(lookupMap(dispatch,V.sn));
            }
            else if (V.lookup === 'card') {
              dispatch(lookupCard(dispatch,V.sn));
            }
            else if (V.lookup === 'ddo') {
              dispatch(lookupDDO(dispatch,method,V.sn));
            }
          }
        }
        dispatch(recordLastValue('lookup',V.lookup));
      },
      didChange: (values,cache) => {
        //console.log("SHOULD CHANGE DID?:",values,cache);
        if(!values.lookupForm)
          return;
        if(!values.lookupForm.values)
          return;
        const V=values.lookupForm.values;
        dispatch(recordLastValue('sn',V.sn));
        if(!cache.method || !cache.lookup || !cache.sn)
            return;

        if(cache.sn != V.sn)
          dispatch(resetOutputBox())
        dispatch(recordLastValue('sn',V.sn));
      },
      onSubmit: (values) => {
          dispatch(submitValues(dispatch,values));
      }
    })
)(LookUpForm);

let RightSide = props => {
    //<Link to="http://korsimoro.com">About Korsimoro</Link><br />
    return (
        <div>
          <Link to="http://korsimoro.com">About Korsimoro</Link><br />
          <Link to="/about">Helsinki 2018 Information</Link><br />
        </div>
    )
}

class Home extends Component {


    render() {
      const { qrcode } = this.props;
      let cardLoad;
      {if ( qrcode.card_load ) {
        cardLoad=<CardLoad />
      }}

      return (
        <Page {...this.props} right={<RightSide />} >
          {cardLoad}
          <LookUpForm />
          <OutputContainer>
            <OutputBox />
          </OutputContainer>
        </Page>
      )
    }
}

export default connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(Home);



