import React from 'react';
import { connect } from 'react-redux';
import NotFound from './NotFound';
import JsonDisplay from './JsonDisplay';
import ServerError from './ServerError';
import NotSupported from './NotSupported';
import Welcome from './Welcome';
import Loading from './Loading';
import MapDisplayBox from './map/MapDisplayBox';
import CardDisplayBox from './CardDisplayBox';

function getBody(lookup,content) {
  if ( content === "notfound" ) {
    return (
      <NotFound />
    )
  }
  else if ( content === "notsupported" ) {
    return (
      <NotSupported />
    )
  }
  else if (content === 'error' ) {
    return (
      <ServerError />
    )
  }
  else if (content === 'json' ) {
    if ( lookup === 'map' ) {
      return (
        <MapDisplayBox />
      )
    }
    else if ( lookup === 'card' ) {
      return (
        <CardDisplayBox />
      )
    }
    return (
      <JsonDisplay />
    )
  }
    return (
      <Welcome />
    )
}

let OutputBox = props => {
    const { server, qrcode,form } = props;
    //console.log("OUTPUT BOX : SERVER =",server,props,form);
    if(server.loading) {
      return (<Loading />);
    }
    if ( qrcode.card_load ) {
      return (
        <div>
          {getBody(server.lookup,server.display)}
        </div>
      )
    }
    else {
      return getBody(server.lookup,server.display);
    }
}

OutputBox = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(OutputBox);


export default OutputBox;



