import React from 'react';
import { connect } from 'react-redux';
import JSONPretty from 'react-json-pretty';
import H18Card from './H18Card';
import Guardianship from './Guardianship';
import Dependency from './Dependency';
import Organization from './Organization';
import Pet from './Pet';


function getBody(type) {
    if ( type === 'korsimoro-h18' ) {
      return (
          <H18Card />
      )
    }
    else if ( type === 'guardianship' ) {
      return (
          <Guardianship />
      )
    }
    else if ( type === 'dependent' ) {
      return (
          <Dependency />
      )
    }
    else if ( type === 'organization' ) {
      return (
          <Organization />
      )
    }
    else if ( type === 'pet' ) {
      return (
          <Pet />
      )
    }
    return null;
}

let MapDisplayBox = props => {
    const { server,form } = props;
    const data = server.output;
    const type = data._t;
    const body = getBody(type);
    const id = form.lookupForm.values.sn;
    if (body !== null) {
      return (
        <div>
          H18 Use Case Metadata for did:h18:{id}<br />
          {body}
        </div>
      )
    }
    return (
        <JSONPretty id="fallback-json" json={server.output}></JSONPretty>
    )
}


MapDisplayBox = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(MapDisplayBox);


export default MapDisplayBox;



