import React from 'react';
import { connect } from 'react-redux';
import About from './About';
import TextLink from 'components/TextLink';
import { Container, Code,List,ListItemLabel,ListItem } from 'css/basicElements';

import { css } from 'react-emotion';

const columnize = css`
    column-count: 3;
    column-span: all;
    column-gap: 45px;
    column-fill: balance;
`;
/*
const columnize = css`
    column-count: 4;
    column-width: 320px;
    column-span: all;
    column-gap: 45px;
    column-fill: balance;
`;
*/

const society = [
{count:1, title:"Banker"},
{count:1, title:"Customs Director"},
{count:1, title:"Digital Identity Director"},
{count:1, title:"Factory Owner"},
{count:1, title:"General"},
{count:1, title:"Head Monk"},
{count:1, title:"Hospital Director"},
{count:1, title:"Immigration Chief"},
{count:1, title:"Importer"},
{count:1, title:"Judge"},
{count:1, title:"King"},
{count:1, title:"Mayor"},
{count:1, title:"Police Chief"},
{count:1, title:"Prince"},
{count:1, title:"Princess"},
{count:1, title:"Queen"},
{count:1, title:"School Director"},
{count:1, title:"Tax Collector"},
{count:2, title:"Doctor"},
{count:2, title:"Lawyer"},
{count:2, title:"Teacher"},
{count:3, title:"Shop Owner"},
{count:4, title:"Factory Worker"},
{count:4, title:"Novice Monk"},
{count:4, title:"Nurse"},
{count:7, title:"Buffalo"},
{count:8, title:"Bureaucrat"},
{count:8, title:"Legal Immigrant"},
{count:8, title:"Refugee"},
{count:10, title:"Police Officer"},
{count:10, title:"Soldier"},
{count:14, title:"Child"},
{count:24, title:"Farmer"}
]

let AboutH18Cards = props => {
      return (
        <About title="Helsinki 2018 Cards and NoT - the Non-Internet of Things">

          <h4>The Levity of Identity</h4>
          <p>
            These cards model a set of real world identities.  As a low-stake game consisting
            only of public information, we are using them to evaluate and
            improve the process of bootstrapping a complete, non-technical
            identity domain governed by a Domain Specific Trust Framework built
            from the Sovrin Foundation Trust Framework.
          </p>
          <p>
            Along the way we are learning valuable technical and trust-framework lessons.
            We hope to improve the technical bootstrapping experience, understand how to
            visualize an identity across multiple systems, and evaluate the interoperability
            of identity systems built around a Domain Specific Trust Framework.
          </p>
          <p>
            This project was conceived in early August 2018 and we are still
            documenting the rules of engagement.  The documentation will be
            available in September 2018, and the functioning system will be
            deployed during October.  Certain components, however, are clear -
            a Cardish identity may be claimed by marking the cardly biometric,
            uploading a photo, and linking it with some PII such as a DID.
            Instructions regarding how to modify the generational
            record (the strip along the bottom) will be provided upon each
            successful claim.
          </p>
          The functioning system includes
          <ol>
            <li>A <TextLink to="/about/resolver">DID Resolution toy method</TextLink>,
            pointing to a managed identity system, which represents a classic IAM honeypot.</li>
            <li>A Domain Specific Trust Framework built upon the Sovrin Foundational
              Trust Framework</li>
            <li>A small data store of card information fronted by a small webapp.</li>
            <li>Metadata associated with each identity which trace the evolution of
              ownership, dependency and guardianship through time.</li>
          </ol>

          <h4>The Story of the Cardish</h4>

          <p>
            Many identities lack both human biometrics and secure cryptographic
            co-processing enclaves.  Such identities lack GPS tracking signals,
            and you can not easily run their fingerprints through an AFIS,
            yet they exist, are unique, are identifiable, can be owned or
            guarded, depend upon others, and in every sense qualify for identity.
          </p>
          <p>
            These identities have no need of private data storage or secure
            P2P messaging.  They were born at
            <TextLink to ="http://www.nuntapun.com/">Nuntapun Printing</TextLink>
            in mid August 2018 and are destined to travel the world before
            becoming irrelevant, lost, or discarded.  In the meantime, they
            card around with their cardly friends, hang out in wallets
            and on desktops, nestle down in the cracks of sofa cushions or
            rest, forgotten, on taxi seats or the tables of conference venues.
            This is the nature of cardish life.
          </p>
          <p>
            Simple as they are, they can be a useful teacher.  Current identity
            system tools remain difficult to use and it is not yet clear how,
            exactly, these tools can be used to model the society of the cardish.
            Any identity management ecosystem capable of managing my international
            KYC information or protecting me from the intrusions of an abusive
            surveilance state should be up to the task of managing the identity
            of the cardish.
          </p>
          <p>
            Using the cardish as stand-ins for more serious and weighty identities,
            such as those of People, Pets, Corporations, and Governments, we hope
            to map out the technical and trust-architectural details of what it
            takes to track, manage, and sustain identity.
          </p>
          <p>
            Along the way we hope to learn a lot about the state of identity art and
            understand which tools are card-positive and which are cardist.
            Which identity systems have implicit reliance upon the biometric sensors
            and cryptographic enclaves of machines manufactured by a limited set
            of manufacturers.  Which identity systems assume you are practiced
            member of the cryptocurrency culture, spending cardly moments
            trading ICOs or otherwise monetizing the value of your identity.
          </p>
          <p>
            The cardish do not worry about secure message passing, as their lives
            are entirely public.  They do worry about modeling the trust relationships
            of their society, and they view their world as an ideal candidate for
            at least one Domain Specific Trust Framework.  Their sole mortal desire is to
            have the trust structure of their cardly culture modeled with verisimilitude
            and respect for their cardish idiosyncracies.
          </p>
          <h4>Cardish Society</h4>
          The following is a list of minted cardish identities.  The society has
          been scattered to the winds, and, through acts of claimation and the
          registration of cardly biometrics, they hope to regain structure to
          their realm.
          <Container>
          <List className={columnize} noBullet>
              {society.map((item, key) => (
                  <ListItem
                      key={key}
                  >
                      <ListItemLabel>{item.count} : {item.title}</ListItemLabel>
                  </ListItem>
              ))}
          </List>
          </Container>
          <h4>Trust Frameworks</h4>
          <p>
            When is a domain specific trust framework warranted, and when can
            trust frameworks be combined?  Can trust frameworks exist at the
            personal level?  The Sovrin Trust Framework defines a system of
            roles for managing trust within an ecosystem.  These roles include:
          </p>
          <ul>
            <li>Trust Framework Authority</li>
            <li>Trust Anchor</li>
            <li>Trust Hub</li>
            <li>Auditor</li>
            <li>Auditor Accreditation Authority</li>
            <li>Currency Exchange</li>
          </ul>
          <p>
            These roles can be combined, or given null values in some cases,
            and in so doing alter dynamics of the
            resulting web of trust and influence the non-technical interoperability
            betwen trust niches.
          </p>
          <p>
            For example, the cardish society is a non-capitalist society that
            does not use money.  They lack a native fiat or crypto currency,
            and so how to interact with Token based identity ecosystems is unclear.
            Does participation in a Sovrin based Domain Specific Trust Framework
            mandate the monetization of identity and force the conception of
            one's identity into a pigeonhole of free-markets and commodities?
            If not, how can we establish a domain specific trust framework
            whereby several of the functions are simplified and collapsed, and how
            does this interact with other identity systems such as uPort or even
            the global Sovrin ecosystem?
          </p>
        </About>
      )
}

AboutH18Cards = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(AboutH18Cards);


export default AboutH18Cards;



