import React from 'react';
import { connect } from 'react-redux';

let Loading = props => {
  const did = "did:"+props.form.lookupForm.values.method+":"+props.form.lookupForm.values.sn;
  const str = ((x) => {
    if(x === 'map') return "Use Case";
    if(x === 'card') return "H18 Card Data";
    return "DID Document";
  })(props.form.lookupForm.values.lookup);
    return (
      <div>
        Loading {str} for {did}.
      </div>
    )
}


Loading = connect(
    state =>({
        ...state
    }),
    dispatch => ({
    })
)(Loading);


export default Loading;



