import React from 'react';
import { connect } from 'react-redux';
import About from './About';
import TextLink from 'components/TextLink';
import { Code } from 'css/basicElements';

let AboutUniversalResolver = props => {
      return (
        <About title="DID Document Resolution">
          <h4>DID Resolution</h4>

          <p>
            DIDs are the backbone of decentralized identity, and are the medium
            through which we can begin to crack the shackles of the managed
            identity citadels which constrain our lives.
            But a DID by itself is meaningless, and the first
            step that needs to be taken to make use of a DID is the extraction of
            information about the identity.
          </p>

          <p>
            The informational meat of a DID is in the DID Document, and the act
            of obtaining the DID Document (or portions of it) when given a DID
            is DID resolution.
          </p>

          <p>
            DID resolution is defined by sub-protocols known as methods, and as
            such DID resolution, generally speaking, involves a wide
            range of technologies and can be extremely complex.
            However, at a very abstract level, DIDs
            are indexes into a simple, abstract, massive, key-value store.
            Every DID, points to an assertion of information about the identified.
          </p>

          <p>
            The <TextLink to="https://github.com/decentralized-identity">DIF's source
            repository</TextLink> contains a universal resolver, with a front end
            that can be used right now at
            <TextLink href="http://uniresolver.io">uniresolver.io</TextLink>.
          </p>

          <p>
            As part of Korsimoro's operations assessment, we are deploying an
            independent instance of universal resolver code base.  As part of
            the project we are adding a toy
            resolver for a local, unsanctioned, toy method - the <Code>h18</Code>
            resolution method.  We are also implementing a simple React front
            end as a PoC exercise of the independence of the universal resolver
            from specific consumers.
          </p>

          <p>
            This work is effectively a
            QA/testing exercise, with an eye towards making these complicated
            toolkits easier to use.  We hope that, in so doing, we can incubate
            interest and action outside of the immediate DIF community.
          </p>

          <p>
            This effort will:
          </p>
          <ol>
            <li>Explore and document deployment requirements</li>
            <li>Allow us to document and fix any bugs and submit pull requests</li>
            <li>Explore the process of adding a non-registered method</li>
          </ol>
        </About>
      )
}

AboutUniversalResolver = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(AboutUniversalResolver);


export default AboutUniversalResolver;



