import React from 'react';
import { connect } from 'react-redux';
import JSONPretty from 'react-json-pretty';
import { Link } from 'react-static';
import TextLink from 'components/TextLink';
import { Code } from 'css/basicElements';

let Pet = props => {
    const { server, form } = props;
    const id = form.lookupForm.values.sn;
    const data = server.output;
    const url = "http://h18.korsimoro.com/assets/"+id+"/"+data.photo;
    return (
      <div>
        <div style={{float:"right"}}>
          <img width="200" height="200" src={url} />
        </div>
        <div>
          <h3 style={{marginTop:0}}>Pet : {data.name}</h3>

        </div>
        <div>
          Why are pets important for digital identity?<br />
          <dl>
            <dt>Microchip registration</dt>
            <dd>
              <p>
              People routinely register their pets with an identity service by
              implanting them with an RFID source placed under the skin.  This
              sort of identity tagging is fragmented and distributed across
              many registries.  This is a classic area for identity management.
            </p>
            </dd>

            <dt>Dependency/Ownership/Guardianship</dt>
            <dd>
              <p>
              A pet is owned and is a dependent.  How do the nascent trust frameworks
              apply, given that they are generally focused on people.  Unlike a person,
              a pet is forever an owned and dependent entity - it will not suddenly
              graduate to <Code>#adulting</Code> and take ownership of it's PKI.
            </p>
            </dd>

            <dt>Non-Technical, Non-Device oriented lives</dt>
            <dd>
              <p>
              Pets are non-technical creatures incapable of registering their
              identities on their phones.  In terms of identity, they are
              like children, except that they can be clearly and unambiguously
              owned.
            </p>
            </dd>

            <dt>Pet Care and Pet Insurance</dt>
            <dd>
              <p>
                The cat in the picture above once moved to the United States, and within
                the first week gave birth to one live kitten.  The birth was complicated
                and a littermate died without being delivered.  This required immediate
                surgery to the tune of $1500, for which we paid via a specially targetted
                pet care credit card.
              </p>
              <p>
                The pet insurance industry (for example
                  <TextLink to="https://www.petinsure.co/">Petinsure.co</TextLink>,
                  <TextLink to="https://www.petinsurance.com/">Petinsurance.com</TextLink>
                ) is an obvious case for integrations with non-human digital
                identity.
              </p>
            </dd>

          </dl>
        </div>
      </div>
    )
}


Pet = connect(
    state =>({
        ...state
    }),
    dispatch => ({
    })
)(Pet);


export default Pet;



