import React from 'react';
import { connect } from 'react-redux';
import About from './About';
import TextLink from 'components/TextLink';

let AboutUseCases = props => {
    return (
      <About title="STF2/DSTF Reference Identity Pool">

        <h4>Reference Identity Pool &amp; Use Case Definitions</h4>

        <p>
          Analysis of real world identity problems can be incredibly complicated.  There are about
          <TextLink to="https://en.wikipedia.org/wiki/World_population">7.6 Billion</TextLink>
          people on the planet, yet if you attend any working group call
          in identity community, you tend to hear about <TextLink to="https://en.wikipedia.org/wiki/Alice_and_Bob">
          Alice and Bob</TextLink>, although sometimes the expanded
          <TextLink to="https://en.wikipedia.org/wiki/Alice_and_Bob#Cast_of_characters">
          Cast of Characters</TextLink> comes out to play.
        </p>

        <p>
          This ensemble is incredibly useful for talking about message exchange
          and privacy preservation, but it does little to structure conversation
          about non-messaging identity issues, such as depedency, guardianship,
          and ownership.  Furthermore, the characters represent
          roles in processes, and are not up to the task of structuring the reference
          cases requried to perform objective, cross-identity system comparisons,
          or evaluation of legal frameworks such as the Sovrin Trust Framework
          and domain specific trust frameworks.
        </p>

        <p>
          The Sovrin Trust Framework introduces concepts of Ownership, Dependency,
          Guardianship, and the
          fact that Organizations, while having identity, can only influence the
          world through the actions of individuals.  Other trust frameworks, such as
          those operated by governments and regulatory agencies tend to be
          overly simplistic, and issues of ownership (such as the titling of an
            automobile) is bound into the bureaucratic spaghetti mess of auto
            financing, insurance, and credit bureaus.  Establishing a clear path
            to ownership and liability is an arduous and expensive task if the
            situation does not fall into one of the cookie-cutter patterns
            expected by the participating institutions.
        </p>

        <p>
          We see value in enumerating and documenting these sorts of cases, and developing
          the ability to replay them against test networks and trust frameworks.  When
          combined with visualization of the resulting trust graphs, these use cases
          will provide a powerful tool for non-technical debugging.
        </p>

        <p>
          It is important to stress that we are not talking about test cases used
          to verify the technical operation of a message passing and secure communications
          system, as Alice, Bob, and the Cast of Characters are adequate to this task.
          Rather, we are talking about real world situations and the content of
          claims and actions relative to the soft, real world goals.
        </p>

        <p>
          For example, I recently discovered that Equifax
          was reporting significantly out of date information about me.  This was
          curious because I have not had credit accounts in the United States for
          a long while.  Attempting
          to engage Equifax to change the information was not possible due to
          their existing tools and procedures, which could not handle my identity
          constellation.  My identity verification was blocked, not because of
          accuracy, but because their data system could not accept my information
          without throwing exceptions.  A similar situation had caused Equifax
          to reject updates to my information - they were simply dropping records.
          Because of this, for quite some time and without my knowledge,
          Equifax had become an errant verifier, unable to fulfill their obligation
          to truthfully report information about me.
        </p>

        <p>
          My Equifax issue reminds us that&nbsp;
          <i>digital identity solutions will always be deployed against a backdrop
          of corporate, governmental, and bureaucratic incompetence.</i>  What exactly happens
          when a trust claim, which is cryptographically provably correct in the world, is nevertheless
          mishandled by a bungling bureaucracy.  Can we visualize what happens to
          a trust network when a botched IT update results in claims verifiers
          producing incorrect verifications?  Can we use this visualization to
          tune and debug the non-technical policy of the trust framework?
        </p>

        <p>
          Reputation systems will come
          in to play, when dealing with large institutions the reputation of the
          verifier will
          only be impacted in the event of a large breach while individual
          participants can be substantially and negatively affected until the
          crisis grows large.
        </p>

        <p>
          It is important to stress that these issues are not issues of failed
          cryptography or privacy compromise.  These are not issues of secure
          communications or protected data storage, or even of authentication
          and authorization.  These are the qualitative issues that impact trust
          in the web of trust itself.
        </p>

        <p>
          In order to sustain our trust in the web of trust itself, the trust
          frameworks and legislation which back them must support the identity
          owners at the expense of largely broken corporations, governments,
          and regulatory institutions.  If we fail to do so we will have only
          changed the technology of identity and the details of the identity
          problems we continue to face.
        </p>

        <p>
          The use of trust-web visualization and the ability to replay test
          suites is an essential tool for ensuring that digital and decentralized
          identity make real, qualitative changes in the system of trust in which
          we are embedded.
        </p>
      </About>
    )
}

AboutUseCases = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(AboutUseCases);


export default AboutUseCases;



