import React from 'react';
import { connect } from 'react-redux';
import About from './About';
import TextLink from 'components/TextLink';

let AboutIndex = props => {
      return (
        <About title="Index">
          <dl>
            <dt>Korsimoro</dt>
            <dd>
              <p>
                <TextLink to="https://en.wikipedia.org/wiki/Korsimoro_Department">Korsimoro</TextLink> is
                both a town and district in <TextLink to="https://en.wikipedia.org/wiki/Burkina_Faso">
                Burkina Faso</TextLink>, just outside of <TextLink to="https://en.wikipedia.org/wiki/Ouagadougou">
                Ougadougou</TextLink>, which, until 2017, hosted <TextLink to="https://en.wikipedia.org/wiki/Panafrican_Film_and_Television_Festival_of_Ouagadougou">
                FESPACO</TextLink>.  The region has been inhabited for nearly 20,000 years and my
                travels there in the 1990's were an eye opening experience which fundamentally
                changed the course of my life and my understanding of humanity.
              </p>
              <p>
                I will forever remember sitting in a household
                of four children, amidst a totality of possessions that could be
                purchased, in their entirety, for less than the tip on a
                wall street friday night bar bill,
                laughing the night away while consuming 1/2 of the family's livestock
                during a dinner honoring our visit.
              </p>
              <p>
                In rememberance of that Burkinabe night, and for a cost far exceeding
                the price of the single chicken we consumed, I registered the name of a
                nearby town, Korsimoro,
                as a <TextLink to="http://korsimoro.com">domain</TextLink> and
                have used it to organize projects supporting the digital and
                decentralized identity community.
              </p>
            </dd>

            <dt>Helsinki 2018, August</dt>
            <dd>
              <p>
              Korsimoro was started in 2015 as a way of organizing assorted dialogues and
              research around the state of digital identity.  We were on hiatus during
              most of 2017 and early 2018.  However, providence and serendepity conspired
              to renew our efforts during Q3 and Q4 of 2018, and specifically at
              MyData2018 and the associated Sovrin Trust Framework meetings.
              </p>
            </dd>

            <dt><TextLink to="/about/h18cards">H18 Cards</TextLink></dt>
            <dd>
              <p>
              If you are here you most likely scanned the QR code on the back of
              an H18 Business Card.  These cards are part of what we call the &nbsp;
              <i>levity of identity</i>.
              Click the link above to read more about the H18 cards.
              </p>
            </dd>

            <dt><TextLink to="/about/resolver">DID Resolution</TextLink></dt>
            <dd>
              <p>
              Our interest in DID Resolution grew out of work with Southeast Asian
              and African financial institutions and markets in 2016.  We realized that
              participation in the global identity platforms was ill suited to the
              reality of identity in these contexts and was directly at odds with
              the need to keep jurisdictional
              data physically within political boundaries and operating only upon
              sanctioned hardware.  We found very few conservative institutions interested
              in pinning sensitive political and financial information to chains such
              as ethereum or bitcoin, or even the global Sovrin network.
              </p>
              <p>
              However, the value of co-opetitive decentralization
              within each jurisdiction was clear and remains of interest.  As such we began
              investigating an internet-of-identity model, whereby jurisdictions
              could run restricted versions of decentralized identity engines yet,
              through appropriate DID mechanics, could remain fully and seamlessly
              interoperable with the maturing suite of global identity solutions.
              </p>
              <p>
                We believe that the emergence of domain specific trust frameworks
                in STF2 is the realization of the Jurisdictional Trust Frameworks
                we promoted at that time.
              </p>
              <p>
                With the development of a robust platform for DID resolution in
                the <TextLink to="https://github.com/decentralized-identity/universal-resolver">
                Universal Resolver</TextLink> project, we see value in serving as
                a deployment testbed, specifically focused on
              </p>
                <ol>
                  <li>Making an instance of a universal resolver trivial to install and maintain</li>
                  <li>Enabling the rapid deployment of test methods pinned to internal systems</li>
                </ol>
              <p>
                We hope that this work will foster widespread adoption of the DID
                strategy and help bridge the gap between managed and decentralized
                identity environments.
              </p>
            </dd>

            <dt><TextLink to="/about/usecases">Trust Framework Test Cases</TextLink></dt>
            <dd>
              <p>
                Every identity system involves a legally binding trust framework,
                whether explicitly presented as in the case of the Sovrin Trust Framework,
                or implicit in the governing jurisdiction's legal and privacy
                management legislation.
              </p>
              <p>
                By combining visualization tools with a rich set of test cases we
                believe we can illustrate the effect of specific trust frameworks
                on the integrity of the web of trust they enable.  In so doing we
                can provide a valuable tool for communicating the impact of specific
                legislation or the adoption of alternative trust frameworks.
              </p>
              <p>
                The ability to test-drive reputation metrics against an active
                suite of test cases is another valuable tool for the community.
                Reputation systems are easy to grasp intuitively, but they are
                hard to reason about objectively.  We see value in the ability to
                visualize reputation dynamics, and the effects of good and bad
                actors, as one evaluates alternative reputation metrics. 
              </p>
            </dd>

          </dl>
        </About>
      )
}

AboutIndex = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(AboutIndex);


export default AboutIndex;



