import { injectGlobal } from 'react-emotion';
import { normalize, transparentize, placeholder, rem } from 'polished';
import { palette, family } from 'css/variables';

/*
if (typeof window !== 'undefined') {
	var WebFont = require('webfontloader');

	WebFont.load({
		google: {
			families: [
            'Work+Sans:300:400:700',
            'Source+Sans+Pro:400,600,700,900'
			]
		},
	});
}
*/

injectGlobal`
    ${normalize()}

    * {
        box-sizing: border-box;
    }

    html,
    body {
        height: 100%;
        min-height: 100%;
    }

    html {
        background-color: ${palette.greenDarkest};
    }

    html {font-size: 1.125em;}

    body {
        font-family: 'Source Sans Pro', sans-serif;
        font-weight: 400;
        line-height: 1.45;
        color: ${palette.grayLighter};
    }

    p {
      margin-top: 1em;
      margin-bottom: 1.3em;
    }

    h1, h2, h3, h4 {
        margin: 1.414em 0 0.5em;
        font-weight: 700;
        line-height: 1;
        font-family: ${family.sourceSansPro};
        font-weight: 900;

        span {
            display: block;
        }
    }

    h1, .h1 {
        margin-top: 0;
        font-size: 6.854em;
        position: relative;
        z-index: 1;
    }

    h2, .h2 {font-size: 4.236em;}

    h3, .h3 {font-size: 2.618em;}

    h4, .h4 {font-size: 1.618em;}

    small, .font_small {font-size: 0.618em;}

    a {
        text-decoration: none;
        color: ${palette.white};
        border-bottom: 1px dotted ${transparentize(0.25, palette.white)}
    }

    hr {
        margin: 48px 0;
        border-color: ${palette.grayLight};
    }

    input,
    select,
    select option,
    textarea {
        width: 100%;
        box-shadow: none;
        border-radius: 2px;
        padding: 6px 8px;
        color: ${palette.grayLighter};
        border: 1px solid ${transparentize(0.75, palette.grayLighter)};
        &:hover,
        &:focus,
        &:active {
            outline: none;
        }
    }

    label {
        display: block;
        font-size: ${rem('12px', '16px')};
        text-transform: uppercase;
        margin-bottom: 6px;
    }

    select {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-color: ${transparentize(0.8, palette.grayLighter)};
        height: 40px;
    }

    textarea {
        width: 100%;
        min-height: 360px;
    }

    input {
        background-color: ${transparentize(0.8, palette.grayLighter)};
        height: 40px;
        &:-webkit-autofill {
            -webkit-box-shadow: 0 0 0px 1000px $#2f3232 inset;
        }

        ${placeholder({'color': palette.grayLighter})}
    }

    #root,
    .App {
        height: inherit;
        min-height: inherit;
    }
`



