import React from 'react';
import { connect } from 'react-redux';
import ObjectInspector from 'react-object-inspector';
import { Link } from 'react-static';
import styled from 'react-emotion';
import {palette} from 'css/variables';
import { rem } from 'polished';
import { Code,List,ListItemLink } from 'css/basicElements';


let CardDisplay = props => {
    const { server, form } = props;
    const id = form.lookupForm.values.sn;
    console.log("S:",server);
    const profile = server.output;
    const url = "http://h18.korsimoro.com/card/"+id+".png"
    return (
      <div>
        <h4>did:h18:{profile.id} --- {profile.title}</h4>
        <p>
        Your card should look like the one below.  The card in your hand is
        an <i>identity playing card</i>.  Each
        card has an identity, space to acquire and grow and cardly biometric,
        and a generational counter along the bottom.
        </p>
        <p>
        In Cardish Society, you are a <Code>{profile.title}</Code>, but what
        exactly this means, is determined by the Cardish Trust Framework, a
        Domain Specific Trust Framework derived from the Sovrin Trust Framework.
        </p>
        <p>
        Born in August 2018, this
        web site will continue
        to grow to support some fun interactions with these cardish identities
        during September and October of 2018.  Feel free to drop a line to the
        email listed on the front of the card for more information or just to say hello.
        </p>
        <Link to="/about/h18cards" >Read more about the Helsinki 2018 cards.</Link>
        <hr />
        <img src={url} width="100%" />
      </div>
    )
}


CardDisplay = connect(
    state =>({
      ...state
    }),
    dispatch => ({
    })
)(CardDisplay);


export default CardDisplay;



