import React from 'react';
import { connect } from 'react-redux';
import ObjectInspector from 'react-object-inspector';

import styled from 'react-emotion';
import {palette} from 'css/variables';
import { rem } from 'polished';

const Pre = styled('pre')`
    font-size: ${rem('14px', '16px')};
    padding: 18px;
    background: ${palette.grayDarker};
`;

function getHeader(type) {
  if (type === 'ddo') {
    return (
      <div style={{marginBottom:"10px"}}>
        DID Document
      </div>
    )
  }
  return (
    <span>
      Unknown Document
    </span>
  )
}

let JsonDisplay = props => {
    const { server, output } = props;
    return (
      <div>
        {getHeader(server.lookup)}
        <ObjectInspector data={server.output} initialExpandedPaths={["*","*.*","*.*.*","*.*.*.*"]} />
      </div>
    )
}


JsonDisplay = connect(
    state =>({
        server: state.server,
        initialValues: { output: ''}
    }),
    dispatch => ({
    })
)(JsonDisplay);


export default JsonDisplay;



