import React from 'react';
import { connect } from 'react-redux';
import JSONPretty from 'react-json-pretty';
import { Link } from 'react-static';

let Organization = props => {
    const { server, form } = props;
    const id = form.lookupForm.values.sn;
    const data = server.output;
    const url = "http://h18.korsimoro.com/assets/"+id+"/"+data.photo;
    return (
      <div>
        <div style={{float:"right"}}>
          <img width="200" height="200" src={url} />
        </div>
        <div>
          <h3 style={{marginTop:0}}>Organization : {data.name}</h3>

        </div>
        <div>
          Why are Organizations important for digital identity?<br />
          <dl>

            <dt></dt>
            <dd>

            </dd>

          </dl>
        </div>
      </div>
    )
}


Organization = connect(
    state =>({
        ...state
    }),
    dispatch => ({
    })
)(Organization);


export default Organization;



