import React from 'react';

export const required = value => (value || typeof value === 'number' ? undefined : 'Required');

export const alphaNumeric = value =>
  value && /[^a-zA-Z0-9]/i.test(value)
    ? 'Only alphanumeric characters'
    : undefined

const maxLength = max => value =>
    value && value.length > max ? `Must be no more than ${max} characters and no whitespace` : undefined;
export const maxLengthLimit = maxLength(512);

const minLength = min => value =>
    value && value.length < min ? `Must be at least ${min} characters and no whitespace` : undefined;
export const minLengthLimit = minLength(1)

export const renderField = ({
    input,
    label,
    type,
    meta: { touched, error, warning }
}) => (
    <div>
        <label>{ label }</label>
        <React.Fragment>
            <input {...input} placeholder={label} type={type} />
            {touched &&
                ((error && <span>{error}</span>) ||
                    (warning && <span>{warning}</span>))}
        </React.Fragment>
    </div>
);



