import React from 'react';
import { connect } from 'react-redux';
import JSONPretty from 'react-json-pretty';
import { Link } from 'react-static';

let Dependency = props => {
    const { server, form } = props;
    const id = form.lookupForm.values.sn;
    const data = server.output;
    const url = "http://h18.korsimoro.com/assets/"+id+"/"+data.photo;
    return (
      <div>
        <div style={{float:"right"}}>
          <img width="200" height="200" src={url} />
        </div>
        <div>
          <h3 style={{marginTop:0}}>Dependent : {data.name}</h3>

        </div>
        <div>
          Issues facing dependents?<br />
          <dl>
            <dt>Dependency vs. Ownership vs. Guardianship</dt>
            <dd>
              Exactly how, in terms of a framework like the
              <Link to="/about/stf">Sovrin Trust Framework</Link> is this
              little animal different from, say, a Pet or Livestock - which
              can be owned, and how is it different from a Refugee, which is
              granted and identity which is managed, most likely, by an
              Organization, rather than a Person.
            </dd>

            <dt>Non-Technical, Non-Device oriented lives</dt>
            <dd>
              Try as you might, little tyke probably won't do the right
              thing signing up for uPort.  They may have better luck with
              bitcoin, Sovrin, or LifeId, but the chances are slim that they
              can make it on their own.
            </dd>

          </dl>
        </div>
      </div>
    )
}


Dependency = connect(
    state =>({
        ...state
    }),
    dispatch => ({
    })
)(Dependency);


export default Dependency;



