const mapApi = `http://h18.korsimoro.com/map/`
const ddoApi = `http://h18.korsimoro.com/did/`
const cardApi = `http://h18.korsimoro.com/card/`
const registryApi = `http://h18.korsimoro.com/registry`

const receiveData = (lookup,data) => {
  return {
      type: 'RECEIVE_DATA',
      data,
      lookup
  }
}
const receiveError = () => {
  return {
      type: 'RECEIVE_ERROR',
  }
}
const receiveRegistry = (data) => {
  return {
      type: 'RECEIVE_REGISTRY',
      registry:data
  }
}
export const resetOutputBox = () => {
  return {
      type: 'RESET_OUTPUT_BOX'
  }
}
export const recordLastValue = (key,value) => {
  return {
      type: 'RECORD_LAST_VALUE',
      key,
      value
  }
}

export const fetchRegistry = (dispatch) => {
  fetch(registryApi)
  .then(response => {
    if (response.status === 200 ) {
    response.json().then(data => {
      dispatch(receiveRegistry(data));
  })
  }
  else {
    dispatch(receiveError());
  }
  })
  .catch(err => {
    console.log("ERROR FETCHING REGISTRY",err);
    dispatch(receiveError());
  });

  return {
      type: 'LOADING',
  }
}

export const lookupMap = (dispatch,sn) => {
    const url = mapApi+sn;
    console.log("Fetching Map Data from",url);
    fetch(url)
    .then(response => {
      if (response.status === 200 ) {
      response.json().then(data => {
        //console.log("GOT DATA:",data);
        dispatch(receiveData('map',data));
    })
    }
    else {
      dispatch(receiveError());
    }
    })
    .catch(err => {
      console.log("ERROR FETCHING MAP",err);
      dispatch(receiveError());
    });

    return {
        type: 'LOADING',
    }
}
export const lookupCard = (dispatch,sn) => {
    const url = mapApi+sn
    console.log("Fetching Card Data from",url);
    fetch(url)
    .then(response => {
      if (response.status === 200 ) {
      response.json().then(data => {
        //console.log("GOT CARD DATA:",data);
        if (data._t != 'korsimoro-h18')
          data = {
            _t:"not found"
          }
        dispatch(receiveData('card',data));
    })
    }
    else {
      dispatch(receiveError());
    }
    })
    .catch(err => {
      console.log("ERROR FETCHING CARD",err);
      dispatch(receiveError());
    });

    return {
        type: 'LOADING',
    }
}

export const lookupDDO = (dispatch,method,sn) => {
    const url = ddoApi+method+"/"+sn;
    console.log("Fetching DDO Data from",url);
    fetch(url)
    .then(response => {
      if (response.status === 200 ) {
      response.json().then(data => {
        dispatch(receiveData('ddo',data));
    })
    }
    else {
      dispatch(receiveError());
    }
    })
    .catch(err => {
      console.log("ERROR FETCHING MAP",err);
      dispatch(receiveError());
    });

    return {
        type: 'LOADING',
    }
}

export const submitValues = (dispatch,values) => {
    if(values.lookup === 'ddo' ) {
      dispatch(lookupDDO(dispatch,values.method,values.sn));
    }
    else if ( values.lookup === 'map') {
      dispatch(lookupMap(dispatch,values.sn));
    }
    else if ( values.lookup === 'card') {
      dispatch(lookupCard(dispatch,values.sn));
    }
    return {
      type: 'SUBMIT_VALUES',
      values
    }
}



