const initialState = {
  sn:undefined,
  lookup:undefined,
  method:undefined
}

export default (state = initialState, action = {}) => {
  switch (action.type) {
    case 'RECORD_LAST_VALUE': {
      const s= {
        ...state,
      }
      s[action.key]=action.value
      return s;
  }
    default:
      return state
  }
}



