const initialState = {
  display:'welcome',
  method:undefined,
  message:undefined,
  output:undefined,
  lookup:undefined,
  loading:false,
  registry:[]
}

export default (state = initialState, action = {}) => {
  switch (action.type) {
    case 'RESET_OUTPUT_BOX': {
      return {
        ...state,
        ...initialState
      }
  }
  case 'LOADING': {
    //console.log("HANDLE LOADING");
    return {
      ...state,
      loading: true,
    }
}
  case 'RECEIVE_REGISTRY': {
    console.log("RECEIVE_REGISTRY");
    return {
      ...state,
      loading: false,
      registry:action.registry
    }
}
    case 'RECEIVE_DATA':
    console.log("RECEIVED JSON FROM SERVER",action.data);
    if ( action.data._t === 'not found' ) {
      return {
        ...state,
        lookup: action.lookup,
        display: "notfound",
        loading:false
      }
    }
    if ( action.data._t === 'not supported' ) {
      return {
        ...state,
        lookup: action.lookup,
        display: "notsupported",
        method:action.data._v,
        message:action.data.message,
        loading:false
      }
    }
       return {
          ...state,
          display: "json",
          output: action.data,
          loading:false,
          lookup: action.lookup,
        }
      case 'RECEIVE_ERROR':
          return {
            ...state,
            display: "error",
            loading:false
          }

    default:
      return state
  }
}



